// import axios from 'axios';

export default {
  namespaced: true,
  state: {
    cart: {
      products: [],
      productCount: 0,
      totalPrice: 0,
      totalAmount: 0
    }
  },
  actions: {
    addProduct (context, data) {
      const products = context.state.cart.products
      let productCount = context.state.cart.productCount
      let totalPrice = 0
      let totalAmount = 0
      if (products.length > 0) {
        const product = products.find(e => e._id === data._id)
        if (product) {
          // ++
          product.quantity += data.quantity
        } else {
          // ++
          products.push(data)
          productCount++
        }
      } else {
        products.push(data)
        productCount++
      }

      //    
      for (const e of products) {
        totalPrice += e.quantity * e.price
        totalAmount += e.quantity
      }
      context.commit('CHANGECART', {
        products,
        productCount,
        totalPrice,
        totalAmount
      })
    },
    removeProduct (context, data) {
      const products = context.state.cart.products
      let productCount = context.state.cart.productCount
      let totalPrice = 0
      let totalAmount = 0

      const product = products.find(e => e._id === data._id)
      if (product) {
        // 
        products.splice(products.indexOf(product), 1)
        productCount = products.length
      } else {
        // 
        this.$swal({
          title: '',
          text: '',
          icon: 'error'
        })
        return
      }

      //    
      for (const e of products) {
        totalPrice += e.quantity * e.price
        totalAmount += e.quantity
      }

      context.commit('CHANGECART', {
        products,
        productCount,
        totalPrice,
        totalAmount
      })
    },
    updateProduct (context, data) {
      const products = context.state.cart.products
      let productCount = context.state.cart.productCount
      let totalPrice = 0
      let totalAmount = 0

      const product = products.find(e => e._id === data._id)
      if (product.quantity > 0) {
        // 
        product.quantity = data.quantity
        productCount = products.length
      } else {
        // 
        context.dispatch('removeProduct', data)
        return
      }

      //    
      for (const e of products) {
        totalPrice += e.quantity * e.price
        totalAmount += e.quantity
      }

      context.commit('CHANGECART', {
        products,
        productCount,
        totalPrice,
        totalAmount
      })
    },
    clearCart (context) {
      context.commit('CHANGECART', {
        products: [],
        productCount: 0,
        totalPrice: 0,
        totalAmount: 0
      })
    }
  },
  mutations: {
    CHANGECART (state, data) {
      state.cart.products = data.products
      state.cart.productCount = data.productCount
      state.cart.totalPrice = data.totalPrice
      state.cart.totalAmount = data.totalAmount
    }
  },
  getters: {
    cart: state => state.cart
  }
}
