<template lang="pug">
  div#app
    router-view(:key="$route.fullPath")
</template>

<script>
import '../src/assets/css/variable.scss'
export default {
  name: 'App',
  computed: {
    user () {
      return this.$store.state.user
    }
  },
  methods: {
    logout () {
      this.axios
        .delete(process.env.VUE_APP_API + '/users/logout')
        .then(res => {
          // 
          if (res.data.success) {
            this.$alert.success('')
            //  vuex
            this.$store.commit('logout')

            // 
            if (this.$route.path !== '/') {
              this.$router.push('/')
            }
          } else {
            this.$alert.error(res.data.message)
          }
        })
        .catch(error => {
          //  200 alert 
          this.$alert.error(error.response.data.message)
        })
    },
    heartbeat () {
      this.axios
        .get(process.env.VUE_APP_API + '/users/heartbeat')
        .then(res => {
          //  vuex 
          if (this.user._id.length > 0) {
            // 
            if (!res.data) {
              this.$alert.error('')
              // 
              this.$store.commit('logout')
              // 
              if (this.$route.path !== '/') {
                this.$router.push('/')
              }
            }
          }
        })
        .catch(err => {
          this.$alert.error('')
          console.log(err)
          // 
          this.$store.commit('logout')
          // 
          if (this.$route.path !== '/') {
            this.$router.push('/')
          }
        })
    }
  },
  mounted () {
    this.heartbeat()
    setInterval(() => {
      this.heartbeat()
    }, 5000)
  }
}
</script>

<style lang="stylus">
@import "./assets/css/_reset.scss"
@media screen and (max-width:768px){
  #topnav{
    display:none
  }
}
</style>
